* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s;
}

button:hover {
  background-color: #2980b9;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.photo-card .info {
  padding: 15px;
}

.photo-card .info p {
  color: #555;
  margin-bottom: 5px;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s;
  display: flex;
}

.load-more:hover {
  background-color: #27ae60;
}

